import { get, post, config } from '@/utils/api.js';
import { savePrize } from '@/common/getData.js';
import { idcardValidate } from '@/common/basicsFun.js';
import customDialog from '@/components/customDialog.vue';
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
export default {
	components: {
		customDialog,
		uniNavBar
	},
	computed: {
		// 当窗口 高度 大于800 是 重新 计算 盒子的上边距
		safeAreaTop() {
			const userSystemInfo = uni.getStorageSync('userSystemInfo');
			const safeAreaTop = userSystemInfo.safeArea.top;
			return safeAreaTop;
		}
	},
	data() {
		name: 'strcode';
		return {
			openid: '',
			staticUrl: config.staticUrl,
			customDialogType: 2, // 提交成功后 提示
			customDialogIsShow: false, // 提交成功后 提示
			userName: '',
			phonenum: '',
			idcard: '',
			phonenumReg: /^1[0-9]{10}$/,
			submitFont: '提交',
			isDisabled: false, //提交按钮 是否 可点击
			isGetPrize: false // 是否已拥有大奖 有（重复扫码） 直接显示信息 没有 显示填写信息
		};
	},
	async onLoad(options) {
		const that = this;
		console.log(options);
		this.isGetPrize = options.isGetPrize || false;
		console.log(this.isGetPrize);
		// 如果已领取大奖 回显信息
		if (this.isGetPrize == 'true') {
			console.log(3333333333);
			const sweepQrcodeData = uni.getStorageSync('sweepQrcodeData');
			if (sweepQrcodeData.reply) {
				that.userName = sweepQrcodeData.reply.username;
				that.phonenum = sweepQrcodeData.reply.phonenum;
				that.idcard = sweepQrcodeData.reply.idcard;
			} else {
				that.userName = '';
				that.phonenum = '';
				that.idcard = '';
			}
			that.submitFont = '您已提交';
			that.isDisabled = true;
		}
	},
	methods: {
		submitFun() {
			const that = this;
			if (that.userName == '' || that.userName == undefined) {
				uni.showModal({
					title: '提示',
					content: '请填写正确的姓名哦！~'
				});
			} else if (!that.phonenumReg.test(that.phonenum)) {
				uni.showModal({
					title: '提示',
					content: '请填写正确的手机号！~'
				});
			} else if (!idcardValidate(that.idcard)) {
				uni.showModal({
					title: '提示',
					content: '请填写正确的身份证号哦！~'
				});
			} else {
				that.submitForm();
			}
		},

		submitForm() {
			const that = this;
			const sweepQrcodeData = uni.getStorageSync('sweepQrcodeData');
			const userData = uni.getStorageSync('userData');
			const sweepQrcodeDataReply = sweepQrcodeData.reply;
			const openid = userData.uinfo.openid;
			console.log(sweepQrcodeData);
			const sendParams = {
				openid: openid,
				skukey: sweepQrcodeDataReply.skukey,
				grandPrizeType: sweepQrcodeDataReply.grandPrizeType,
				prizeVcode: sweepQrcodeDataReply.prizeVcode,
				username: this.userName,
				idcard: this.idcard,
				phonenum: this.phonenum
			};
			savePrize(sendParams).then(res => {
				console.log('');
				console.log(res);
				if (res) {
					//  显示成功 提示
					that.customDialogType = 2;
					that.customDialogIsShow = true;
					// 弹窗 启动动画
					that.$refs.customDialogChild.isStartAnimationFun(true);
				}
			});
		},
		updateCustomDialogColse(data) {
			const that = this;
			that.customDialogIsShow = false;
			that.submitFont = '您已提交';
			that.isDisabled = true;
		},
		// 返回
		back() {
			uni.switchTab({
				url: '../index/index'
			});
		}
	}
};
